import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
      <section id="testimonials" className="s-testimonials target-section">
        <div className="s-testimonials__bg" />
        <div className="row s-testimonials__header">
          <div className="column large-12">
            <h3>Hear What others say</h3>
          </div>
        </div>
        <div className="row s-testimonials__content">
          <div className="column">
            <div className="swiper-container testimonial-slider">
              <div className="swiper-wrapper">
                <div className="testimonial-slider__slide swiper-slide">
                  <div className="testimonial-slider__author">
                    <img src="images/avatars/venky.jpg" alt="Author image" className="testimonial-slider__avatar" />
                    <cite className="testimonial-slider__cite">
                      <strong>Venkatesh Varadachari</strong>
                      <span>Founder, Makerdemy</span>
                    </cite>
                  </div>
                  <p>
                  Aish is one of those quiet, effective workers who gets things done. During her stint at Makerdemy, I was impressed by her ability to learn new tech related topics fast and on her own. Aish is that rare low maintenance, high performance employee most organizations are keen to hire. She also has this ability to work well in a team setting. <br></br>Incidentally, Aish won the "intern of the year" award at Makerdemy during her stint with us. All in all, Aish is someone you would want in your team. I would highly recommend her, without any reservations.
                  </p>
                </div> {/* end testimonial-slider__slide */}
              </div> {/* end testimonial slider swiper-wrapper */}
              <div className="swiper-pagination" />
            </div> {/* end swiper-container */}
          </div> {/* end column */}
        </div> {/* end row */}
      </section>
      </React.Fragment>
    );
  }
}