import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
      <section id="contact" className="s-contact target-section">
        <div className="row s-contact__header">
          <div className="column large-12">
            <h3 className="section-header-allcaps">Reach out to me today</h3>
          </div>
        </div>
        <div className="row s-contact__content">
          <div className="column large-7 medium-12">
            <h4 className="huge-text">
              Want to hire me? I am just an email away!
            </h4>
          </div>
          <div className="column large-4 medium-12">
            <div className="row contact-infos">
              <div className="column large-12 medium-6 tab-12">
                <div className="contact-block">
                  <h5 className="contact-block__header">
                    Email
                  </h5>
                  <p className="contact-block__content">
                    <a className="mailtoui" href="mailto:aishwaryasrisankaran@gmail.com">aishwaryasrisankaran@gmail.com</a>
                  </p>
                </div> {/* end contact-block */}
              </div>
              <div className="column large-12 medium-6 tab-12">
                <div className="contact-block">
                  <h5 className="contact-block__header">
                    Phone
                  </h5>
                  <p className="contact-block__content">
                    <a href="tel:+14084779364">+1(408) 477-9364</a>
                  </p>
                </div> {/* end contact-block */}
              </div>
              <div className="column large-12">
                <a href="https://www.linkedin.com/in/aishwarya-srisankaran/" className="mailtoui btn btn--primary h-full-width" target="_blank" rel="noopener noreferrer">Connect with me</a>
              </div>
            </div> {/* end contact-infos */}
          </div>
        </div> {/* s-contact__content */}
      </section>
      </React.Fragment>
    );
  }
}
